






















import Vue from 'vue'
import { mapActions } from 'vuex'
import { get } from 'lodash'

export default Vue.extend({
  name: 'summary-table',
  props: {
    columns: { required: true },
    rows: { required: true }
  },
  data () {
    return {
      pagination: {
        rowsPerPage: 10
      }
    }
  },
  computed: {
    loading (): boolean {
      return !get(this.columns, 'length') || !get(this.rows, 'length')
    }
  },
  methods: {
    ...mapActions(['NewSummaryRecords'])
  }
})
